.info-picture-box {
    display: -ms-flexbox;
    display: flex;
    border: 20px solid #011A2A;
    background-color: #4A1213;
}

.ipb-div {
    width: 50%;
    height: 436px;
}

.ipb-info {
    background-color: #4A1213;
    color: #FFFFFF;
}

.ipb-picture {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    background-color: #4A1213;
}

.ipb-image {
    max-height: calc(100% - 24px);
    max-width: calc(100% - 24px);
    border: 12px solid #4A1213;
    border-radius: 30px;
}

.ipb-info-content {
    padding: 24px 56px;
}

ul {
    font-size: 19px;
}

@media screen and (max-width: 1200px) {
    ul {
        font-size: 18px;
    }
}

@media screen and (max-width: 1040px) {
    ul {
        font-size: 16px;
    }
}

@media screen and (max-width: 960px) {
    .ipb-info-content {
        padding: 12px 24px;
    }

    ul {
        font-size: 14px;
    }
}

@media screen and (max-width: 768px) {
    .info-picture-box.left {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .info-picture-box.right {
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    }

    .ipb-div {
        width: 100%;
    }

    .ipb-info {
        height: 100%;
    }
}

@media screen and (max-width: 540px) {
    .ipb-picture {
        height: 335px;
    }
}


