.hero {
    border: 12px solid #011A2A;
    color: #FFEEA5;
    position: relative;
    font-family: 'Song Myung', serif;
}

.hero-image {
    max-height: 100%;
    max-width: 100%;
}

.hero-text {
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 60%;
    left: 80%;
    font-size: 40px;
    white-space: nowrap;
}

@media screen and (max-width: 768px) {
    .hero-text {
        font-size: 32px;
        left: 75%;
    }
}

@media screen and (max-width: 540px) {
    .hero-text {
        font-size: 24px;
        left: 65%;
    }
}


